.s-partners {
    padding: rem(77) 0 rem(102);

    .l-default {
        max-width: rem(1220);
    }

    .title {
        margin-bottom: rem(78);
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        @include tr("all");
        display: flex;
        align-items: center;
        justify-content: center;
        width: 20%;
        min-height: rem(157);
        opacity: 0.3;
        border-left: rem(1) solid $grey_dark;
        padding: 0 rem(14);

        &:nth-of-type(5n + 1) {
            border-left: none;
        }

        &:first-child {
            border-left: none;
        }

        img {
            display: block;
            max-width: rem(162);
        }

        &:hover {
            opacity: 1;

            img {
                filter: brightness(0) invert(1);
            }
        }
    }
    .text-center {
        text-align: center;
    }
}
