.footer {
    padding-bottom: rem(50);
    background: linear-gradient(357.79deg, #000000, rgba(40, 46, 66, 0) 69.85%);

    .form {
        width: 100%;
        margin-bottom: rem(64);

        .btn {
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: rem(44);
            min-width: rem(167);
        }
        &__inputs {
            display: flex;
            align-items: center;
            margin-bottom: rem(23);
        }

        &__input {
            width: 100%;
            margin-right: rem(20);
            input {
                width: 100%;
                min-height: rem(44);
                padding-left: rem(23);
                color: $grey_light_more_2;
                background: none;
                border: rem(1) solid $grey;
                border-radius: rem(10);

                &::placeholder {
                    color: $grey_light_more_2;
                }
            }
        }

        &__checkbox {
            font-size: rem(15);
            font-weight: 500;
            line-height: 120%;
            color: $grey_light_more;
        }
    }

    &__top {
        padding-bottom: rem(15);
        border-bottom: rem(1) solid $grey_light;
    }

    &__content {
        display: flex;
        margin-bottom: rem(122);
        padding-top: rem(50);
    }

    &__info {
        max-width: rem(524);
        margin-right: rem(100);
    }

    &__logo {
        margin-bottom: rem(40);
        font-size: rem(30);
        line-height: 1;
    }

    &__text {
        margin-bottom: rem(33);
        font-size: rem(15);
        font-weight: 600;
        line-height: 120%;
        color: $grey_light_more;
    }

    &__address {
        margin-bottom: rem(46);
        font-size: rem(20);
        font-weight: 900;
        line-height: 120%;
    }

    &__contact {
        display: flex;
        align-items: center;

        .btn {
            max-width: 100%;
            padding: rem(15) rem(33);
            color: $grey_light_more_2;
            white-space: nowrap;
        }
    }

    &__email {
        margin-right: rem(16);
        font-size: rem(40);
        font-weight: 900;
        line-height: 1;
        color: $grey;
        white-space: nowrap;
    }

    &__nav {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;
        margin: 0 rem(-50);
        flex-wrap: wrap;

        &-column {
            width: calc(33.3333% - rem(100));
            margin: 0 rem(50);
        }

        &-title {
            display: block;
            font-size: rem(20);
            font-weight: 600;
            line-height: 120%;
            color: $grey_white;

            &:nth-child(2) {
                margin-top: rem(67);
            }
        }

        &-list {
            margin-top: rem(30);
        }

        &-item {
            display: block;
            margin-bottom: rem(12);
            font-size: rem(15);
            line-height: 120%;
            color: $grey_light;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__bottom {
        display: flex;
        color: $grey;

        &-item {
            display: flex;
            align-items: center;
            font-size: rem(15);
            line-height: 120%;

            &:after {
                display: block;
                width: rem(1);
                height: rem(22);
                margin: 0 rem(15);
                background: $grey;
                content: "";
            }

            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
    }

    &__copyrights {
        margin-left: auto;
        font-size: rem(15);
        line-height: 120%;
    }
}
