.news-detail {
    padding: rem(59) 0 rem(80);
    background-image: url(/img/news/back.jpg);
    background-repeat: no-repeat;
    // background-size: contain;
    background-position: center;
    &__container {
        display: flex;
        justify-content: flex-start;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: rem(-1);
            left: 0;
            width: 50%;
            height: rem(2);
            background: $black_light;
        }
        &::after {
            content: "";
            position: absolute;
            top: rem(-1);
            left: 0;
            width: 19%;
            height: rem(2);
            transition: all 0.4s ease;
            background: #f4f4f2;
        }
        &:hover {
            &::after {
                width: 50%;
                background: #d91745;
            }
        }
    }
    &__date {
        margin-right: 123px;
        @media (max-width: 1024px) {
            margin-right: rem(86);
        }
        @media (max-width: 600px) {
            display: none;
        }
        &--day {
            font-weight: 700;
            font-size: rem(55);
            line-height: 1.2;

            color: $grey_light_more;
        }
        &--month {
            font-weight: 300;
            font-size: rem(19);
            line-height: 1.2;

            color: $grey_white;
        }
    }
    &__content {
        margin-top: rem(28);
        &--img {
            margin-bottom: rem(60);
            @media (max-width: 1024px) {
                margin-bottom: rem(50);
            }
            @media (max-width: 768px) {
                margin-bottom: rem(35);
            }
            @media (max-width: 600px) {
                display: flex;
            }
        }
        &--title {
            font-weight: 700;
            font-size: rem(25);

            line-height: 1.2;

            margin-bottom: rem(20);
            @media (max-width: 1024px) {
                font-size: rem(23);
                margin-bottom: rem(37);
            }
            @media (max-width: 768px) {
                font-size: rem(19);
                margin-bottom: rem(20);
            }
            @media (max-width: 600px) {
                font-size: rem(17);
            }
        }
        &--text {
            margin-bottom: rem(160);
            @media (max-width: 1024px) {
                margin-bottom: rem(120);
            }
            @media (max-width: 768px) {
                margin-bottom: rem(100);
            }
            @media (max-width: 600px) {
                margin-bottom: rem(64);
            }
        }
    }
    &__text {
        &--paragraph {
            font-weight: 300;
            font-size: rem(19);
            color: $grey_light_more;

            margin-bottom: rem(20);
            @media (max-width: 1024px) {
                font-size: rem(17);
                margin-bottom: rem(15);
            }
            @media (max-width: 768px) {
                font-size: rem(15);
                margin-bottom: rem(10);
            }
            @media (max-width: 600px) {
                font-size: rem(13);
            }
        }
    }
    &__more {
        display: flex;
        justify-content: space-between;
        &--btn {
            display: flex;
            font-size: rem(40);

            color: $grey_white;
        }
        &--all {
            svg {
                transform: scale(-1, 1);
            }
        }
    }
    &__btn {
        &--next {
            transform: rotate(180deg);
            margin-left: rem(50);
        }
    }
    &__topic {
        display: none;
        margin-right: rem(33);
        @media (max-width: 600px) {
            display: block;
        }
        &--day {
            font-weight: 700;
            font-size: rem(50);
            line-height: 1.2;

            color: $grey_light_more;
        }
        &--month {
            font-weight: 300;
            font-size: rem(17);
            line-height: 1.2;

            color: $grey_white;
        }
    }
}
