.popup {
    display: none;

    &-form {
        max-width: rem(430);
        padding: rem(57) rem(65) rem(65);
        background: linear-gradient(
            36deg,
            rgba(66, 67, 70, 1) 0%,
            rgba(29, 30, 33, 1) 100%
        );
        border-radius: rem(20);
        box-shadow: 0 rem(4) rem(4) rgba(0, 0, 0, 0.25);

        .form {
            .focused {
                label {
                    color: $white;
                }
            }

            &__title {
                position: relative;
                max-width: fit-content;
                margin-bottom: rem(30);
                padding-bottom: rem(3);
                font-size: rem(21);
                font-weight: 500;
                line-height: 1.2;

                &::after {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: rem(1);
                    background: $red;
                    content: "";
                }
            }

            &__inputs {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
            }

            &__input {
                display: flex;
                width: 100%;
                margin-bottom: rem(20);
                flex-direction: column;

                label {
                    margin-bottom: rem(5);
                    font-size: rem(15);
                    line-height: 1;
                    color: $grey_light;
                    @include tr("all");

                    &.error {
                        font-size: rem(8);
                        line-height: 1.2;
                        color: $red !important;
                    }
                }

                input {
                    min-height: rem(30);
                    padding: 0 rem(12);
                    font-size: rem(14);
                    color: $white;
                    background: none;
                    border: rem(1) solid $grey;
                    border-radius: rem(5);
                    @include tr("all");

                    &:focus {
                        color: $white;
                        border: rem(1) solid $white;
                    }

                    &::placeholder {
                        font-size: rem(12);
                        color: $grey_light;
                    }
                }
            }

            &__submit {
                padding: rem(10) rem(12);
                font-size: rem(15);
                line-height: 1;
            }
        }
    }
}
