.s-home-products {
    position: relative;
    margin-top: rem(-40);
    padding-bottom: rem(67);

    .product {
        display: flex;
        flex-direction: column-reverse;
        justify-content: flex-end;

        @media (min-width: 1600px) {
            flex-direction: column;
        }

        &__link {
            display: flex;
            align-items: center;
            justify-content: center;
            width: rem(40);
            height: rem(40);
            border: rem(2) solid $grey_light;
            margin-bottom: rem(37);
            border-radius: 100%;
        }

        &__top {
            display: flex;
            justify-content: flex-end;
            position: relative;
            margin-top: auto;

            &-text {
                transform: translateX(rem(-30));
                font-size: rem(120);
                font-weight: 900;
                line-height: 1;
                text-transform: uppercase;
                color: #4b4b4e;
            }

            &-caption {
                display: flex;
                position: absolute;
                z-index: 1;
                top: 50%;
                left: 50%;
                width: 100%;
                overflow: hidden;
                transform: translate(-50%, -50%);
            }
        }

        &__info {
            padding: rem(45) rem(35) rem(24);

            @media (min-width: 992px) {
                padding: rem(54) rem(45) rem(24);
            }

            &-item, &-title {
                display: none;
                // animation: show 1.5s ease-in-out;
            }

            &-description {
                font-size: rem(10);
                line-height: 1.2;
                color: $grey_light_more;
            }
        }

        &__made {
            display: none;
        }

        &__img {
            z-index: 2;
            width: auto;
            height: rem(180);
            transition: all 1s ease-in-out;

            @media (min-width: 992px) {
                height: rem(230);
                min-height: rem(230);
                margin-right: rem(-30);
            }

            img {
                display: block;
                width: auto;
                height: 100%;
            }
        }

        &__name {
            margin-bottom: rem(14);
            font-size: rem(40);
            font-weight: 900;
            line-height: 1;
            color: $grey_light_more_2;
        }

        &__subtitle {
            display: block;
            margin-bottom: rem(9);
            font-size: rem(10);
            line-height: 1.2;
            color: $grey_white;
        }

        &.swiper-slide-active {
            // @media (min-width: 992px) {
            //   flex-direction: column;
            // }

            &:after {
                // background-image: url('.././img/svg/noize-large.svg');
            }

            .product {
                &__top {
                    display: flex;
                    justify-content: flex-end;
                    position: relative;

                    &-text {
                        transform: translateX(0);
                        font-size: rem(110);
                        font-weight: 900;
                        line-height: 1;
                        text-transform: uppercase;
                        color: #4b4b4e;

                        @media (min-width: 992px) {
                            font-size: rem(220);
                        }

                        @media (min-width: 1200px) {
                            font-size: rem(260);
                        }
                    }

                    &-caption {
                        display: flex;
                        position: absolute;
                        z-index: 1;
                        top: 33%;
                        left: 0;
                        width: 100%;
                        overflow: hidden;
                        transform: translateY(-70%);

                        @media (min-width: 768px) {
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                }

                &__img {
                    @media (min-width: 1600px) {
                        min-height: 357px;
                        margin-top: rem(-50);
                        margin-right: rem(-30);
                        margin-left: auto;
                    }
                }

                &__info {
                    padding: rem(45) rem(35) rem(24);

                    @media (min-width: 992px) {
                        padding-right: rem(70);
                        padding-bottom: rem(43);
                        padding-left: rem(55);
                    }

                    &-column {
                        @media (min-width: 768px) {
                            margin-right: rem(30);
                        }

                        &:last-child {
                            display: flex;
                            align-items: flex-end;
                            justify-content: flex-end;
                            margin-left: auto;
                        }
                    }

                    &-columns {
                        display: flex;
                        flex-wrap: wrap;
                        flex-direction: column;
                        gap: rem(20);

                        @media (min-width: 768px) {
                            flex-direction: row;
                        }

                        @media (min-width: 1200px) {
                            flex-wrap: nowrap;
                        }
                    }

                    &-title {
                        margin-bottom: rem(13);
                        font-family: $secondary_font;
                        font-size: rem(10);
                        font-weight: 900;
                        line-height: 1;
                        color: $grey_light_more;
                    }

                    &-caption, &-text {
                        font-family: $secondary_font;
                        font-size: rem(10);
                        line-height: 1.2;
                        color: $grey_light_more;
                    }

                    &-text {
                        font-weight: 900;
                    }

                    &-item {
                        display: block;
                        margin-bottom: rem(5);
                    }

                    &-description {
                        max-width: 194px;
                    }
                }

                &__name {
                    display: flex;
                    align-items: center;
                    margin-bottom: rem(14);

                    &:after {
                        content: "";
                        display: block;
                        width: rem(50);
                        height: rem(1);
                        margin-left: rem(27);
                        background: #050303;
                    }
                }

                &__made {
                    display: none;

                    @media (min-width: 768px) {
                        display: block;
                        font-size: rem(14);
                        font-weight: 600;
                        color: $grey_white;
                    }
                }

                &__link {
                    margin-bottom: 0;
                }
            }
        }
    }

    .swiper-slide {
        position: relative;
        height: auto;
        opacity: 0;
        // transition-property: width, opacity, background-image;
        // transition-duration: .8s;
        // transition-timing-function: ease-in;
        // transition: opacity 0.8s ease-in-out;
        // filter: drop-shadow(6px rem(10) rem(6) rgba(0, 0, 0, 0.25));
        background-image: url("../img/global/noise-mobile.png");
        background-size: cover;

        @media (min-width: 768px) {
            background-image: url("../img/global/noise.png");
        }

        // &:after {
        //   z-index: -1;
        //   position: absolute;
        //   top: 0;
        //   left: 0;
        //   width: 100%;
        //   height: 100%;
        //   background: #3f3f42;
        //   opacity: 0.9;
        //   content: "";
        //   background-image: url('.././img/svg/noize.svg');
        //   background-size: cover;
        // }

        &-active {
            box-shadow: 6px 10px 6px rgba(0, 0, 0, 0.25);
            // width: 75% !important;
            opacity: 1;

            @media (min-width: 768px) {
                width: rem(650) !important;
            }

            @media (min-width: 992px) {
                width: rem(850) !important;
            }

            @media (min-width: 1200px) {
                width: rem(1088) !important;
            }
        }

        &-next {
            // margin-right: rem(70) !important;
            opacity: 1;

            // @media (min-width: 768px) {
            //   width: calc(25% - 30px) !important;
            // }
        }
    }

    .swiper-pagination {
        top: rem(-50);
        text-align: left;

        &-bullet {
            width: rem(15);
            height: rem(15);
            margin-right: rem(26) !important;
            background: #d9d9d9;
            opacity: 0.4;

            &-active {
                opacity: 1;
            }
        }
    }

    &__slider {
        height: rem(541);
        overflow: visible;
        animation: show 0.4s ease;
        margin-top: rem(40);
    }

    &__sliders {
        &-container {
            position: relative;
            width: 100%;
            min-height: rem(540);
            margin-bottom: rem(50);

            &:after {
                content: "";
                position: absolute;
                top: 80%;
                left: rem(-70);
                width: calc(100% +140px);
                height: rem(54);
                transform: translateY(-80%);
                background: $red;
            }
        }
    }

    &__categories {
        padding-top: rem(35);
        border-top: rem(2) solid #4d4d4d;

        &-wrapper {
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            align-items: center;
            margin: 0 rem(-1);

            @media (min-width: 768px) {
                flex-direction: row;
            }
        }
    }

    &__category {
        @include tr("all");
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 100%;
        margin: 0 rem(1) rem(2);
        padding: rem(13) rem(4);
        cursor: pointer;
        font-size: rem(14);
        text-align: center;
        color: $grey_light;
        background: $black_light;

        @media (min-width: 768px) {
            width: calc(33.33% - 2px);
            font-size: rem(15);
        }

        @media (min-width: 992px) {
            width: calc(25% - 2px);
        }

        &:hover {
            color: $grey_dark;
            background: $grey_light_more;
        }

        &.is-disabled {
            color: $grey_dark;
            pointer-events: none;
        }

        &.active {
            color: $grey_dark;
            background: $grey_light_more;

            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: rem(4);
                height: 100%;
                background: $red;
            }
        }
    }
}
