$white: #f4f4f2;
$black: #1e1e1d;

.main-top {
    padding-top: rem(72);
    background: url(".././img/main-top/bg.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;

    .title {
        text-align: center;
        color: $grey_white;

        span {
            opacity: 0;
            animation: fadeIn 2s alternate infinite;
        }
    }

    &__notebook {
        position: relative;
        margin-top: rem(50);

        &-img {
            position: relative;
            z-index: 3;
        }

        &-video {
            position: absolute;
            z-index: 1;
            top: 7%;
            left: 48%;
            width: 33.3333%;
            transform: translateY(-7%) translateX(-48%);
            opacity: 0.8;
        }

        &:before {
            content: "";
            position: absolute;
            z-index: 4;
            bottom: 0;
            left: 0;
            width: 30%;
            height: 33.3333%;
            transform: rotate(180deg);
            background: linear-gradient(
                90deg,
                rgba(17, 18, 23, 0) 0%,
                rgba(7, 6, 12, 1) 100%
            );
        }

        &:after {
            content: "";
            position: absolute;
            z-index: 3;
            right: 0;
            bottom: 0;
            width: 30%;
            height: 33.3333%;
            background: linear-gradient(
                90deg,
                rgba(17, 18, 23, 0) 0%,
                rgba(7, 6, 12, 1) 100%
            );
        }
    }

    &__more {
        display: flex;
        align-items: center;
        justify-content: center;

        @media (min-width: 768px) {
            justify-content: flex-start;
            position: absolute;
            top: 55%;
            left: rem(70);
        }

        &-text {
            font-size: rem(14);
            line-height: 1;
            color: $white;
        }

        &-icon {
            margin-left: rem(15);
            font-size: rem(9);
            line-height: 1;
        }
    }

    &__bottom {
        padding: rem(23) 0 rem(35);
        font-size: rem(21);
        line-height: 1.2;
        text-align: center;
        color: $grey;
        background: linear-gradient(
            0deg,
            rgba(1, 1, 2, 1) 0%,
            rgba(4, 5, 5, 0) 100%
        );

        &-text {
            font-size: rem(15);
            line-height: rem(18);
        }
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        filter: blur(10px);
    }
    50%,
    100% {
        opacity: 1;
        filter: blur(0);
    }
}
